import React from "react";
import { useDispatch } from "react-redux";
import { googleSignIn } from "../../../../../redux/auth";
import { StyledIconButton } from "../../../../Header/HeaderStyle";
import {
  ImageContainer,
  OAuthButton,
  OAuthButtonContainer,
} from "../../authStyle";
import googleLogo from "./icon-google.png";

export default function GoogleOAuth() {
  const dispatch = useDispatch();
  const onClick = () => {
    dispatch(googleSignIn());
  };
  return (
    <OAuthButtonContainer>
      <StyledIconButton onClick={onClick}>
        <ImageContainer>
          <OAuthButton alt="google-login" src={googleLogo} />
        </ImageContainer>
      </StyledIconButton>
      OR
    </OAuthButtonContainer>
  );
}
