import React, { useState } from "react";

import Login from "./Components/Login";
import Register from "./Components/Register";
import ForgotPassword from "./Components/ForgotPassword";
import ForgotPasswordCheckEmail from "./Components/ForgotPasswordCheckEmail";
import ForgotPasswordVerify from "./Components/ForgotPasswordVerify";
import ChangePasswordConfirm from "./Components/ChangePasswordConfirm";

import { Container, SubContainer, LogoContainer } from "./authStyle";
import { useDispatch } from "react-redux";
import { closeAuth } from "../../../redux/auth";

const initialPath = () =>
  window.location.pathname === "/forgotPasswordVerify/"
    ? "forgotPasswordVerify"
    : "login";

export default function AuthMain() {
  const dispatch = useDispatch();
  const [height, setHeight] = useState(540);
  const [slideUp, setSlideUp] = useState(false);
  const [page, setPage] = useState(initialPath());

  const onClose = (e) => {
    e.preventDefault();
    dispatch(closeAuth());
  };
  const onStopPropagation = (e) => {
    e.stopPropagation();
  };
  const onSuccess = () => {
    setSlideUp(true);
  };

  return (
    <Container onClick={onClose}>
      <SubContainer
        onClick={onStopPropagation}
        height={height}
        slideUp={slideUp}
      >
        <LogoContainer>Weather Forecast</LogoContainer>
        {
          {
            login: (
              <Login
                onSuccess={onSuccess}
                setHeight={setHeight}
                onClose={onClose}
                setPage={setPage}
              />
            ),
            register: (
              <Register
                onSuccess={onSuccess}
                onClose={onClose}
                setHeight={setHeight}
                setPage={setPage}
              />
            ),
            forgotPassword: (
              <ForgotPassword onClose={onClose} setPage={setPage} />
            ),
            forgotPasswordCheckEmail: (
              <ForgotPasswordCheckEmail
                setHeight={setHeight}
                setPage={setPage}
              />
            ),
            forgotPasswordVerify: (
              <ForgotPasswordVerify
                onClose={onClose}
                setHeight={setHeight}
                setPage={setPage}
              />
            ),
            changePasswordConfirm: (
              <ChangePasswordConfirm setHeight={setHeight} setPage={setPage} />
            ),
          }[page]
        }
      </SubContainer>
    </Container>
  );
}
