import { useContainerWidth } from "../../../globalHooks/useContainerWidth";
import { AxisContainer } from "./BrushStyle";
import Dates from "./Dates";

export default function Axis({ range, axis }) {
  const containerWidth = useContainerWidth("#axis-container");
  return (
    <AxisContainer id="axis-container">
      <Dates range={range} axis={axis} containerWidth={containerWidth} />
    </AxisContainer>
  );
}
