import React from "react";
import { Info } from "../CardStyle";
import Delete from "./Delete/Delete";
import Drag from "./Drag/Drag";

export default function CardInfo({ location, handleProps }) {
  return (
    <Info>
      <Drag handleProps={handleProps} />
      {location.name}
      <Delete id={location.id} />
    </Info>
  );
}
