import React, { useEffect, useMemo, useRef } from "react";
import moment from "moment";
import { AreaChart, Area, Tooltip, YAxis } from "recharts";
import {
  Background,
  Cloud,
  CustomTooltipContainer,
  Label,
  Rain,
  StyledCard,
  Temperature,
} from "./CardStyle";
import { useCallback } from "react";
import CardInfo from "./CardInfo/CardInfo";

export default function MobileCard({
  location,
  width,
  range,
  setZoom,
  setDragCard,
  handleProps,
}) {
  const areaChartRef = useRef();

  const [minTemp, tempRange, forecast] = useMemo(() => {
    let maxTemp = -Infinity;
    let minTemp = Infinity;
    const data = location.values.slice(range[0], range[1] + 1);
    data.forEach((hour) => {
      if (hour.temp < minTemp) minTemp = hour.temp;
      else if (hour.temp > maxTemp) maxTemp = hour.temp;
    });
    const tempRange = maxTemp - minTemp;
    const formattedData = data.map((data) => {
      return {
        ...data,
        temp: Math.min(
          Math.round((100 * (data.temp - minTemp)) / tempRange),
          100
        ),
      };
    });
    return [minTemp, tempRange, formattedData];
  }, [location, range]);

  const CustomTooltip = useCallback(
    ({ active, payload, label }) => {
      if (active && payload && payload.length) {
        return (
          <CustomTooltipContainer>
            <Label>{moment(forecast[label].time).format("ddd D MMM hA")}</Label>
            <Temperature>
              Temperature:{" "}
              {Math.round((payload[2].value / 100) * tempRange + minTemp) +
                "°C"}
            </Temperature>
            <Rain>Chance of Rain: {payload[3].value + "%"}</Rain>
            <Cloud>Cloud Coverage: {payload[1].value + "%"}</Cloud>
          </CustomTooltipContainer>
        );
      }

      return null;
    },
    [forecast, minTemp, tempRange]
  );

  useEffect(() => {
    const preventDefault = (e) => e.preventDefault();
    const ref = areaChartRef.current;
    areaChartRef.current.addEventListener("wheel", preventDefault);
    return () => {
      ref.addEventListener("wheel", preventDefault);
    };
  }, []);

  return (
    <StyledCard>
      <Background
        onWheel={setZoom}
        className="card"
        ref={areaChartRef}
        onPointerDown={setDragCard}
      >
        <AreaChart
          width={width - 10}
          height={200}
          data={forecast}
          margin={{ top: 1, right: 0, left: 0, bottom: 0 }}
        >
          <defs>
            <linearGradient id="cloud" x1="0" y1="1" x2="0" y2="0">
              <stop offset="0%" stopColor="ghostwhite" stopOpacity={1} />
              <stop offset="100%" stopColor="ghostwhite" stopOpacity={0.7} />
            </linearGradient>
          </defs>
          <Tooltip content={<CustomTooltip />} />
          <YAxis yAxisId={0} hide={true} />
          <YAxis
            width={30}
            yAxisId={1}
            interval={0}
            padding={{ top: 10, bottom: 10 }}
            ticks={[Math.round(minTemp), Math.round(minTemp + tempRange)]}
            label="°C"
            domain={[Math.round(minTemp), Math.round(minTemp + tempRange)]}
          />
          <Area
            isAnimationActive={false}
            type="monotone"
            dataKey="isNight"
            stroke="white"
            fill="hsl(232, 23%, 23%)"
            fillOpacity={1}
          />
          <Area
            isAnimationActive={false}
            type="monotone"
            dataKey="cloud"
            stroke="white"
            fill="url(#cloud)"
            fillOpacity={1}
          />
          <Area
            isAnimationActive={false}
            type="monotone"
            dataKey="temp"
            stroke="gold"
            fillOpacity={0}
            strokeWidth={3}
          />
          <Area
            isAnimationActive={false}
            type="monotone"
            dataKey="rainChance"
            stroke="dodgerblue"
            fillOpacity={0}
            strokeWidth={3}
          />
        </AreaChart>
      </Background>
      <CardInfo location={location} handleProps={handleProps} />
    </StyledCard>
  );
}
