import { createSlice, createEntityAdapter } from "@reduxjs/toolkit";
import {
  firebaseGetForecasts,
  firebaseSearchLocation,
  dbApi,
} from "../api/firebase";

let lastVisible = Date.now();
// let lastVisible = 1629278033;
let loading;
const locationsAdapter = createEntityAdapter({
  sortComparer: (a, b) => b.order - a.order,
});

export const initialState = locationsAdapter.getInitialState({
  loading: true,
  finished: false,
});

const locations = createSlice({
  name: "locations",
  initialState,
  reducers: {
    addLocation(state, action) {
      locationsAdapter.upsertOne(state, action.payload);
    },
    addLocations(state, action) {
      locationsAdapter.upsertMany(state, action.payload);
    },
    setFinished(state) {
      state.finished = true;
    },
    setLoading(state) {
      state.loading = true;
    },
    setLoaded(state) {
      state.loading = false;
    },
    deleteOne(state, action) {
      locationsAdapter.removeOne(state, action.payload);
    },
    clearData(state) {
      locationsAdapter.removeAll(state);
      lastVisible = Date.now();
      // lastVisible = 1629278033;
      loading = false;
      state.finished = false;
    },
    updateOne(state, action) {
      locationsAdapter.updateOne(state, action.payload);
    },
  },
});

export const {
  addLocation,
  addLocations,
  setFinished,
  setLoading,
  setLoaded,
  deleteOne,
  clearData,
  updateOne,
} = locations.actions;

export const { selectAll: selectAllLocations } = locationsAdapter.getSelectors(
  (state) => state.locations
);

export default locations.reducer;

export const getForecasts = () => async (dispatch) => {
  if (loading) return;
  loading = true;
  try {
    const { data } = await firebaseGetForecasts({ lastVisible });
    dispatch(setLoaded());
    if (data) {
      data.sort((a, b) => b.order - a.order);
      lastVisible = data[data.length - 1].order;
      dispatch(addLocations(data));
    }
    if (!data || data.length < 5) dispatch(setFinished());
  } catch (error) {
    console.log(error);
  }
  loading = false;
};

export const searchLocation = (searchQuery) => async (dispatch) => {
  try {
    const { data } = await firebaseSearchLocation({ searchQuery });
    if (data) {
      dispatch(addLocations(data));
      return true;
    } else return false;
  } catch (error) {
    console.log(error);
  }
};

export const deleteLocation = (id) => async (dispatch, getState) => {
  const user = getState().auth.user;
  dispatch(deleteOne(id));
  if (!user) return;

  dbApi.collection("users").doc(user).collection("locations").doc(id).delete();
};

export const updateOrder = (id, newOrder) => async (dispatch, getState) => {
  dispatch(updateOne({ id, changes: { order: newOrder } }));
  const user = getState().auth.user;
  if (!user) return;

  dbApi
    .collection("users")
    .doc(user)
    .collection("locations")
    .doc(id)
    .update({ order: newOrder });
};
