import React, { useEffect, useState } from "react";
import useMediaQuery from "@material-ui/core/useMediaQuery";
import { DateBox } from "./BrushStyle";

export default function Dates({ axis, range, containerWidth, brush }) {
  const isDesktop = useMediaQuery("(min-width:700px)");
  const [boxes, setBoxes] = useState([]);
  useEffect(() => {
    let leftEdge = 0;
    let rightEdge = 0;
    const boxes = [];
    const data = range
      ? axis.values.slice(range[0], range[1] + 1)
      : axis.values;
    let day = data[0].day;
    data.forEach((time, index) => {
      if (time.hour === "00") {
        rightEdge = (100 * index) / data.length;
        let width = rightEdge - leftEdge;
        boxes.push({ leftEdge, width, day });
        day = time.day;
        leftEdge = rightEdge;
      }
    });
    boxes.push({ leftEdge, width: 100 - leftEdge, day });
    setBoxes(boxes);
  }, [axis, range, containerWidth]);

  return (
    <>
      {boxes.map((box, index) => (
        <DateBox
          key={index}
          left={box.leftEdge}
          width={box.width}
          containerWidth={containerWidth}
        >
          {box.width < 5
            ? ""
            : brush || isDesktop
            ? box.day
            : box.day.slice(0, 3)}
        </DateBox>
      ))}
    </>
  );
}
