import React from "react";
import { useSelector } from "react-redux";
import { CircularProgress } from "@material-ui/core";

import { ListContainer, NoContent } from "./SiteBodyStyle";
import { selectAllLocations } from "../../redux/locations";

import LocationsList from "./LocationsList/LocationsList";
import Brush from "./Brush/Brush";
import { useLocations } from "./useLocations";
import { useState } from "react";
import Axis from "./Brush/Axis";
import { Container } from "./Brush/BrushStyle";

export default function SiteBody() {
  useLocations();
  const locations = useSelector(selectAllLocations);
  const loading = useSelector(({ locations }) => locations.loading);
  const [range, setRange] = useState([0, 1]);
  const [zoom, setZoom] = useState(null);
  const [dragCard, setDragCard] = useState(null);
  return (
    <>
      {loading ? (
        <ListContainer>
          <CircularProgress color="inherit" size={100} />
        </ListContainer>
      ) : locations.length ? (
        <>
          <Container>
            <Brush
              axis={locations[0]}
              range={range}
              setRange={setRange}
              zoom={zoom}
              dragCard={dragCard}
            />
            <Axis range={range} axis={locations[0]} brush={false} />
          </Container>
          <LocationsList
            locations={locations}
            range={range}
            setZoom={setZoom}
            setDragCard={setDragCard}
          />
        </>
      ) : (
        <ListContainer>
          <NoContent>Use the search bar to add a new location.</NoContent>
          <NoContent>Login to save locations.</NoContent>
        </ListContainer>
      )}
    </>
  );
}
