import styled from "styled-components";

export const Form = styled.form`
  position: relative;
`;
export const Input = styled.input`
  width: 750px;
  max-width: calc(100vw - 95px);
  height: 45px;
  border-radius: 15px;
  border: none;
  outline: none;
  padding: 10px 10px 5px 70px;
  background-color: transparent !important;
  color: ${(props) => (props.disabled ? "grey" : props.theme.primaryColor)};
  box-shadow: inset 8px 8px 8px ${({ theme }) => theme.topShadow},
    inset -8px -8px 8px ${({ theme }) => theme.bottomShadow};
  font-weight: 700;
  font-size: 1rem;
  ::placeholder {
    color: ${(props) => (props.error ? "red" : props.theme.primaryColor)};
  }
`;
export const LoadingContainer = styled.div`
  position: absolute;
  left: 10px;
  top: 5px;
  height: 35px;
  width: 35px;
  color: ${({ theme }) => theme.primaryColor};
`;
