import { useMediaQuery } from "@material-ui/core";
import React, { useRef, useState } from "react";
import { useEffect } from "react";
import { useMemo } from "react";
import { useContainerWidth } from "../../../globalHooks/useContainerWidth";

import { BrushBox, BrushContainer, BrushHandle } from "./BrushStyle";
import Dates from "./Dates";
import { useMouseInterface } from "./useMouseInterface";

export default function Brush({ axis, range, setRange, zoom, dragCard }) {
  const isDesktop = useMediaQuery("(min-width:700px)");

  const [left, setLeft] = useState(0);
  const [width, setWidth] = useState(1);

  useEffect(() => {
    if (isDesktop) {
      setWidth(25);
    } else setWidth(10);
  }, [isDesktop]);

  const containerWidth = useContainerWidth("#brush-container");

  const boxRef = useRef(null);
  const containerRef = useRef(null);

  const [
    onPointerDownMove,
    onPointerDownHandleLeft,
    onPointerDownRightHandle,
    onClick,
    onWheel,
    onPointerDownChartMove,
  ] = useMouseInterface(boxRef, containerRef, setWidth, setLeft);

  const percentageValues = useMemo(
    () => axis.values.map((value, index) => (100 * index) / axis.values.length),
    [axis]
  );
  useEffect(() => {
    if (zoom) onWheel(zoom);
  }, [zoom, onWheel]);
  useEffect(() => {
    if (dragCard) onPointerDownChartMove(dragCard);
  }, [dragCard, onPointerDownChartMove]);

  useEffect(() => {
    const rightPerc = left + width;
    const leftPerc = left;
    let leftIndex = null;
    let rightIndex = null;
    for (let i = 0; i < percentageValues.length; i++) {
      if (leftIndex === null && percentageValues[i] >= leftPerc) {
        leftIndex = i;
      } else if (
        rightIndex === null &&
        (i === percentageValues.length - 1 ||
          percentageValues[i + 1] > rightPerc)
      ) {
        rightIndex = i;
        break;
      }
    }
    setRange([leftIndex, rightIndex]);
  }, [left, width, percentageValues, setRange]);

  return (
    <BrushContainer
      id="brush-container"
      ref={containerRef}
      onPointerDown={onClick}
    >
      <Dates axis={axis} containerWidth={containerWidth} brush={true} />
      <BrushBox
        ref={boxRef}
        left={left}
        width={width}
        containerWidth={containerWidth}
        onPointerDown={onPointerDownMove}
      >
        <BrushHandle left onPointerDown={onPointerDownHandleLeft} />
        <BrushHandle right onPointerDown={onPointerDownRightHandle} />
      </BrushBox>
    </BrushContainer>
  );
}
