import styled from "styled-components";

const topShadow = "#5B5B5B";
const bottomShadow = "#ffffff";

export const Container = styled.div`
  position: absolute;
  width: calc(100% - 15px);
  height: 220px;
  z-index: 2;
  background-color: #ecf0f3;
  padding: 100px 15px 0 15px;
  border-bottom: 1px ridge grey;
  @media (max-width: 700px) {
    width: 100%;
    padding: 90px 5px 0 5px;
    height: 188px;
  }
`;
export const BrushContainer = styled.div`
  position: relative;
  width: 100%;
  height: 50px;
  background: #b2fefa; /* fallback for old browsers */
  background: -webkit-linear-gradient(
    to top,
    #0ed2f7,
    #b2fefa
  ); /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to top, #0ed2f7, #b2fefa);
  border-radius: 25px;
  overflow: hidden;
  display: flex;
  -webkit-touch-callout: none; /* iOS Safari */
  -webkit-user-select: none; /* Safari */
  -khtml-user-select: none; /* Konqueror HTML */
  -moz-user-select: none; /* Old versions of Firefox */
  -ms-user-select: none; /* Internet Explorer/Edge */
  user-select: none; /* Non-prefixed version, currently
                                  supported by Chrome, Edge, Opera and Firefox */
  box-shadow: 5px 5px 15px ${topShadow}, -5px -5px 15px ${bottomShadow};
  font-size: 0.7rem;
  @media (max-width: 700px) {
    border-radius: 15px;
  }
`;
export const AxisContainer = styled.div`
  margin: 37px 0 0 260px;
  position: relative;
  width: calc(100% - 260px);
  height: 32px;
  overflow: hidden;
  display: flex;
  -webkit-touch-callout: none; /* iOS Safari */
  -webkit-user-select: none; /* Safari */
  -khtml-user-select: none; /* Konqueror HTML */
  -moz-user-select: none; /* Old versions of Firefox */
  -ms-user-select: none; /* Internet Explorer/Edge */
  user-select: none; /* Non-prefixed version, currently
                                  supported by Chrome, Edge, Opera and Firefox */
  box-shadow: -13px -13px 20px ${({ theme }) => theme.bottomShadow};
  background-color: #ecf0f3;
  border-top-left-radius: 15px;
  border-top-right-radius: 15px;
  font-weight: 700;
  color: ${({ theme }) => theme.primaryColor};
  @media (max-width: 700px) {
    width: calc(100% - 30px);
    margin: 15px 0 0 30px;
  }
`;
export const BrushBox = styled.div.attrs((props) => ({
  style: {
    width: props.width + "%",
    transform: `translateX(${(props.left * props.containerWidth) / 100}px)`,
  },
}))`
  position: absolute;
  height: 100%;
  cursor: move;
  box-shadow: 0 0 0 100vw rgba(0, 0, 0, 0.4);
  border-left: 3px solid white;
  border-right: 3px solid white;
`;
export const BrushHandle = styled.div`
  position: absolute;
  left: ${(props) => (props.left ? "-5px" : "")};
  right: ${(props) => (props.right ? "-5px" : "")};
  width: 10px;
  height: 100%;
  cursor: ew-resize;
`;
export const DateBox = styled.div.attrs((props) => ({
  style: {
    width: props.width + "%",
    transform: `translateX(${(props.left * props.containerWidth) / 100}px)`,
  },
}))`
  height: 100%;
  position: absolute;
  box-sizing: border-box;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  padding: 5px;
  overflow: hidden;
  :not(:first-child) {
    border-left: 1px solid grey;
  }
`;
