import { useRef, useCallback } from "react";
import _ from "lodash";

const MARGIN_LEFT = 15;

export const useMouseInterface = (boxRef, containerRef, setWidth, setLeft) => {
  const boxLeft = useRef(null);
  const boxRight = useRef(null);
  const boxWidth = useRef(null);
  const mouseStart = useRef(false);

  //
  // ─── MOVE ───────────────────────────────────────────────────────────────────────
  //

  const onPointerDownMove = useCallback(
    (e) => {
      e.stopPropagation();
      const handleMouseMove = (e) => {
        let newLeft = boxLeft.current + e.clientX - mouseStart.current;
        if (newLeft < 0) newLeft = 0;
        if (newLeft > boxRight.current) newLeft = boxRight.current;
        setLeft((newLeft * 100) / totalWidth);
      };

      const throttledMouseMove = _.throttle(handleMouseMove, 50, {
        trailing: true,
      });

      const onPointerUp = () => {
        document.removeEventListener("pointerup", onPointerUp, true);
        document.removeEventListener("pointermove", throttledMouseMove, true);
      };

      const { left, width } = boxRef.current.getBoundingClientRect();
      const { right, width: totalWidth } =
        containerRef.current.getBoundingClientRect();
      boxLeft.current = left - MARGIN_LEFT;
      boxRight.current = right - MARGIN_LEFT - width;
      mouseStart.current = e.clientX;
      document.addEventListener("pointermove", throttledMouseMove, true);
      document.addEventListener("pointerup", onPointerUp, true);
    },
    [boxRef, containerRef, setLeft]
  );

  //
  // ─── LEFT HANDLE ────────────────────────────────────────────────────────────────
  //

  const onPointerDownHandleLeft = useCallback(
    (e) => {
      const handleMouseMove = (e) => {
        let newLeft = boxLeft.current + e.clientX - mouseStart.current;
        if (newLeft < 0) newLeft = 0;
        const newWidth = boxWidth.current + boxLeft.current - newLeft;
        if (newWidth < 50) {
          setLeft((100 * (boxRight.current - 50)) / totalWidth);
          setWidth((50 * 100) / totalWidth);
        } else {
          setLeft((newLeft * 100) / totalWidth);
          setWidth((newWidth * 100) / totalWidth);
        }
      };

      const throttledExpandLeft = _.throttle(handleMouseMove, 50, {
        trailing: true,
      });

      const onPointerUp = () => {
        document.removeEventListener("pointerup", onPointerUp, true);
        document.removeEventListener("pointermove", throttledExpandLeft, true);
      };

      e.stopPropagation();
      const { left, right, width } = boxRef.current.getBoundingClientRect();
      const { width: totalWidth } =
        containerRef.current.getBoundingClientRect();
      boxLeft.current = left - MARGIN_LEFT;
      boxRight.current = right - MARGIN_LEFT;
      boxWidth.current = width;
      mouseStart.current = e.clientX;
      document.addEventListener("pointermove", throttledExpandLeft, true);
      document.addEventListener("pointerup", onPointerUp, true);
    },
    [boxRef, containerRef, setLeft, setWidth]
  );

  //
  // ─── RIGHT HANDLE ───────────────────────────────────────────────────────────────
  //

  const onPointerDownRightHandle = useCallback(
    (e) => {
      const handleMouseMove = (e) => {
        let newRight = boxLeft.current + e.clientX - mouseStart.current;
        if (newRight > boxRight.current) newRight = boxRight.current;
        const newWidth = boxWidth.current - boxLeft.current + newRight;
        if (newWidth < 50) {
          setWidth((50 * 100) / totalWidth);
        } else {
          setWidth((newWidth * 100) / totalWidth);
        }
      };
      const throttledExpandRight = _.throttle(handleMouseMove, 50, {
        trailing: true,
      });

      const onPointerUp = () => {
        document.removeEventListener("pointerup", onPointerUp, true);
        document.removeEventListener("pointermove", throttledExpandRight, true);
      };

      e.stopPropagation();
      const { right: left, width } = boxRef.current.getBoundingClientRect();
      const { right, width: totalWidth } =
        containerRef.current.getBoundingClientRect();
      boxLeft.current = left - MARGIN_LEFT;
      boxRight.current = right - MARGIN_LEFT;
      boxWidth.current = width;
      mouseStart.current = e.clientX;
      document.addEventListener("pointermove", throttledExpandRight, true);
      document.addEventListener("pointerup", onPointerUp, true);
    },
    [boxRef, containerRef, setWidth]
  );

  //
  // ─── CLICK ──────────────────────────────────────────────────────────────────────
  //
  const onClick = useCallback(
    (e) => {
      const { width } = boxRef.current.getBoundingClientRect();
      const { right, width: totalWidth } =
        containerRef.current.getBoundingClientRect();
      const boxRight = right - MARGIN_LEFT - width;
      let newLeft = e.clientX - MARGIN_LEFT - width / 2;
      if (newLeft < 0) newLeft = 0;
      else if (newLeft > boxRight) newLeft = boxRight;
      setLeft((newLeft * 100) / totalWidth);
    },
    [boxRef, containerRef, setLeft]
  );

  //
  // ─── ZOOM ───────────────────────────────────────────────────────────────────────
  //
  const onWheel = useCallback(
    (e) => {
      const zoomIn = () => {
        let newWidth = width * 0.8;
        if (newWidth < 50) newWidth = 50;
        let newLeft = left - MARGIN_LEFT + e.ratio * (width - newWidth);
        setWidth((newWidth * 100) / totalWidth);
        setLeft((newLeft * 100) / totalWidth);
      };
      const zoomOut = () => {
        let newWidth = width * 1.25;
        if (newWidth > totalWidth) newWidth = totalWidth;
        let newLeft = left - MARGIN_LEFT + e.ratio * (width - newWidth);
        setWidth((newWidth * 100) / totalWidth);
        if (newLeft < 0) {
          newLeft = 0;
        } else if (newLeft > containerRight - newWidth - MARGIN_LEFT) {
          newLeft = containerRight - newWidth - MARGIN_LEFT;
        }
        setLeft((newLeft * 100) / totalWidth);
      };

      const { left, width } = boxRef.current.getBoundingClientRect();
      const { width: totalWidth, right: containerRight } =
        containerRef.current.getBoundingClientRect();

      const parent = document.querySelector(".card");
      const cardMargin = 60;
      let { left: cardLeft, width: cardWidth } = parent.getBoundingClientRect();
      cardLeft = e.clientX - cardLeft - cardMargin;
      e.ratio = cardLeft / cardWidth;
      if (e.deltaY < 0) zoomIn();
      else zoomOut();
    },
    [boxRef, containerRef, setWidth, setLeft]
  );

  //
  // ─── MOVE ───────────────────────────────────────────────────────────────────────
  //

  const onPointerDownChartMove = useCallback(
    (e) => {
      const handleMouseMove = (e) => {
        let newLeft =
          boxLeft.current +
          (width / cardWidth) * (mouseStart.current - e.clientX);
        if (newLeft < 0) newLeft = 0;
        if (newLeft > boxRight.current) newLeft = boxRight.current;
        setLeft((newLeft * 100) / totalWidth);
      };

      const throttledMouseMove = _.throttle(handleMouseMove, 50, {
        trailing: true,
      });

      const onPointerUp = () => {
        document.removeEventListener("pointerup", onPointerUp, true);
        document.removeEventListener("pointermove", throttledMouseMove, true);
      };

      const { left, width } = boxRef.current.getBoundingClientRect();
      const { right, width: totalWidth } =
        containerRef.current.getBoundingClientRect();
      boxLeft.current = left - MARGIN_LEFT;
      boxRight.current = right - MARGIN_LEFT - width;
      mouseStart.current = e.clientX;

      const card = document.querySelector(".card");
      const cardWidth = card.getBoundingClientRect().width;

      document.addEventListener("pointermove", throttledMouseMove, true);
      document.addEventListener("pointerup", onPointerUp, true);
    },
    [boxRef, containerRef, setLeft]
  );

  return [
    onPointerDownMove,
    onPointerDownHandleLeft,
    onPointerDownRightHandle,
    onClick,
    onWheel,
    onPointerDownChartMove,
  ];
};
