import React from "react";
import { useState } from "react";
import { useDispatch } from "react-redux";
import { searchLocation } from "../../../../redux/locations";
import { Form, Input, LoadingContainer } from "./SearchBarStyle";
import { CircularProgress } from "@material-ui/core";
import SearchIcon from "@material-ui/icons/Search";

export default function SearchBar() {
  const dispatch = useDispatch();

  const [searchText, setSearchText] = useState("");
  const [disabled, setDisabled] = useState(false);
  const [error, setError] = useState(false);
  const [placeholder, setPlaceholder] = useState("Add a new location...");

  const onSearchTextChange = (e) => {
    if (!e.target.value) {
      setError(false);
      setPlaceholder("Add a new location...");
    }
    setSearchText(e.target.value);
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    setDisabled(true);
    const res = await dispatch(searchLocation(searchText));
    setDisabled(false);
    setSearchText("");

    if (res) {
      setError(false);
      setPlaceholder("Add a new location...");
    } else {
      setError(true);
      setPlaceholder("Location not found");
    }
  };
  return (
    <Form onSubmit={handleSubmit}>
      <Input
        type="text"
        id="search"
        placeholder={placeholder}
        value={searchText}
        onChange={onSearchTextChange}
        autoComplete="off"
        disabled={disabled}
        error={error}
      />
      <LoadingContainer>
        {disabled ? (
          <CircularProgress size={35} color="inherit" />
        ) : (
          <SearchIcon fontSize="large" />
        )}
      </LoadingContainer>
    </Form>
  );
}
