import "./LocationsList.css";
import React, { useCallback, useMemo } from "react";
import { useDispatch, useSelector } from "react-redux";
import { FixedSizeList as List, areEqual } from "react-window";
import InfiniteLoader from "react-window-infinite-loader";
import { DragDropContext, Droppable, Draggable } from "react-beautiful-dnd";
import { useMediaQuery } from "@material-ui/core";

import { getForecasts, updateOrder } from "../../../redux/locations";
import { useContainerHeight } from "../../../globalHooks/useContainerHeight";
import { useContainerWidth } from "../../../globalHooks/useContainerWidth";
import Card from "./Location/Card";
import MobileCard from "./MobileLocation/MobileCard";
import Loading from "./Loading";

const InnerElement = (props) => {
  return <div className="innerElement" {...props} />;
};

function Item({
  provided,
  item,
  style,
  isDragging,
  width,
  range,
  setZoom,
  setDragCard,
  isDesktop,
}) {
  const Component = useMemo(
    () => (!item ? Loading : isDesktop ? Card : MobileCard),
    [isDesktop, item]
  );
  return (
    <div
      {...provided.draggableProps}
      ref={provided.innerRef}
      style={{
        ...style,
        ...provided.draggableProps.style,
      }}
      className="item"
    >
      <Component
        location={item}
        width={width}
        range={range}
        setZoom={setZoom}
        setDragCard={setDragCard}
        handleProps={provided.dragHandleProps}
      />
    </div>
  );
}

const Row = React.memo(function Row(props) {
  const { data, index, style } = props;
  const { items, width, range, setZoom, setDragCard, isDesktop } = data;
  const item = items[index];
  const key = item ? item.id : "loadingID";
  return (
    <Draggable draggableId={key} index={index} key={key}>
      {(provided) => (
        <Item
          provided={provided}
          item={item}
          style={style}
          width={width}
          range={range}
          setZoom={setZoom}
          setDragCard={setDragCard}
          isDesktop={isDesktop}
        />
      )}
    </Draggable>
  );
}, areEqual);

export default function LocationsList({
  locations,
  range,
  setZoom,
  setDragCard,
}) {
  const dispatch = useDispatch();

  const hasNextPage = useSelector(({ locations }) => !locations.finished);

  const isDesktop = useMediaQuery("(min-width:700px)");

  const itemCount = useMemo(() => {
    return hasNextPage ? locations.length + 1 : locations.length;
  }, [hasNextPage, locations]);

  const loadMoreItems = useCallback(() => {
    dispatch(getForecasts());
  }, [dispatch]);

  const isItemLoaded = useCallback(
    (index) => !hasNextPage || index < locations.length,
    [hasNextPage, locations]
  );

  const height = useContainerHeight("body");
  const width = useContainerWidth(".list");

  const onDragEnd = useCallback(
    (result) => {
      if (!result.destination) {
        return;
      }
      if (result.source.index === result.destination.index) {
        return;
      }

      let nextOrder, prevOrder;
      if (result.source.index < result.destination.index) {
        //move down the list
        let nextIndex = result.destination.index + 1;
        nextOrder = locations[nextIndex]
          ? locations[nextIndex].order
          : locations[result.destination.index].order - 1000;
        prevOrder = locations[result.destination.index].order;
      } else {
        //move up the list
        let prevIndex = result.destination.index - 1;
        prevOrder = locations[prevIndex]
          ? locations[prevIndex].order
          : locations[result.destination.index].order + 1000;
        nextOrder = locations[result.destination.index].order;
      }
      const newOrder = Math.floor((nextOrder + prevOrder) / 2);

      dispatch(updateOrder(locations[result.source.index].id, newOrder));
    },
    [dispatch, locations]
  );

  return (
    <InfiniteLoader
      isItemLoaded={isItemLoaded}
      itemCount={itemCount}
      loadMoreItems={loadMoreItems}
      threshold={1}
    >
      {({ onItemsRendered, ref }) => (
        <DragDropContext onDragEnd={onDragEnd}>
          <Droppable
            droppableId="droppable"
            mode="virtual"
            renderClone={(provided, snapshot, rubric) => {
              return (
                <Item
                  provided={provided}
                  isDragging={snapshot.isDragging}
                  item={locations[rubric.source.index]}
                  width={width}
                  range={range}
                  setZoom={setZoom}
                  setDragCard={setDragCard}
                  isDesktop={isDesktop}
                />
              );
            }}
          >
            {(provided) => (
              <List
                className="list"
                height={height}
                itemCount={itemCount}
                itemSize={isDesktop ? 240 : 320}
                onItemsRendered={onItemsRendered}
                ref={ref}
                width="100%"
                innerElementType={InnerElement}
                outerRef={provided.innerRef}
                itemData={{
                  items: locations,
                  width,
                  range,
                  setZoom,
                  setDragCard,
                  isDesktop,
                }}
              >
                {Row}
              </List>
            )}
          </Droppable>
        </DragDropContext>
      )}
    </InfiniteLoader>
  );
}
